<template>
<div>
    <p-nav-vue></p-nav-vue>
    <v-container>
        <v-toolbar>
            <v-spacer></v-spacer>
            <v-toolbar-title class="edt-prof-txt"> Transactions </v-toolbar-title>
            <v-spacer></v-spacer>
        </v-toolbar>
        <v-data-table hide-actions :headers="headers" :items="transactions" class="elevation-1">
            <template slot="items" slot-scope="props">
                <td> {{ props.item.transID }} </td>
                <td class="text-xs-right">{{ props.item.amount }}</td>
                <td class="text-xs-right">{{ props.item.transDate }}</td>
                <td class="text-xs-right">{{ props.item.status }}</td>
            </template>
        </v-data-table>
    </v-container>
    </div>
</template>

<script>
import Nav from "./PointNav";
import Vue from 'vue';
import firebase from 'firebase'
import { db } from "../main";
export default {
    components: {
        'p-nav-vue': Nav
    },
    data(){
        return {
            headers:[
            {
                text:'Transaction  ID',
                align: 'left',
                sortable: false,
                value: 'transID'
            },
            {text:'Amount', value:'amount'},
            {text: 'Transaction Date', value:'transDate'},
            {text: 'Status', value:'status'}
            ],
            transactions: []
        }
    },
    created(){
        firebase.auth().onAuthStateChanged(
            (user) => {
                db.collection('transactions').where("witPointId", "==", user.uid).get().then(
            (querySnapshot) => {
                querySnapshot.forEach(
                    (doc) => {
                        
                        var transUserStat='';
                        if(doc.data().userTransStat == true && doc.data().pointTransStat == true){
                            transUserStat =''
                        }else if(doc.data().userTransStat == true && doc.data().pointTransStat == false) {
                            transUserStat =''
                        } else if(doc.data().userTransStat ==false){
                            transUserStat =''
                        }
                        this.transactions.push(
                            {
                    value: false,
                    transID: doc.id,
                    amount: doc.data().amount,
                    witID: doc.data().witID,
                    
                    transDate: doc.data().transDate,
                    status: transUserStat
                }
                        )

                    })
            }).catch( (error) => {
                console.log(error)
            })
            }
        )
        
    },
}
</script>

<style scoped>
    @import url('https://fonts.googleapis.com/css?family=Bitter');

    .edt-prof-txt {
        font-family: 'Bitter', serif;
    }
</style>





