<template>
    <v-layout>
        <v-navigation-drawer fixed v-model="drawer" app>
            <v-container fluid>
            <v-layout justify-center>
                <v-flex text-xs-center>
                <v-avatar slot="activator" size="100px">
                    <img src="https://www.soccercric.com/uploads/news/images/1309164275952e2e897191.png" alt="Avatar Image">
                </v-avatar><br><br>
                <v-text>{{ email }}</v-text>
                </v-flex>
            </v-layout>
            </v-container>
            <v-list dense>
            <router-link class="links" to="/home">
                <v-list-tile>
                    <v-list-tile-action>
                    <v-icon>home</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                    <v-list-tile-title>Home</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </router-link>
            <router-link class="links" to="/profile">
                <v-list-tile>
                    <v-list-tile-action>
                    <v-icon>account_box</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                    <v-list-tile-title>Profile</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </router-link>
            <router-link class="links" to="/transactions">
                <v-list-tile>
                    <v-list-tile-action>
                    <v-icon>credit_card</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                    <v-list-tile-title>Transactions</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </router-link>
            <a class="links" href="mailto:paymoning@gmail.com">
                <v-list-tile>
                    <v-list-tile-action>
                    <v-icon>contact_mail</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                    <v-list-tile-title>Contact PayMoni</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </a>

                <v-list-tile v-on:click='signOut'>
                    <v-list-tile-action>
                    <v-icon>power_settings_new</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                    <v-list-tile-title>Logout</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </v-list>
            <v-container fluid>
            <v-layout justify-start>
                <v-footer color="#00FFFF" app>
                <span class="" style="color:black">&copy; 2019 PayMoni Ltd.</span>
                </v-footer>
            </v-layout>
            </v-container>
        </v-navigation-drawer>
        <v-toolbar color="#00FFFF" dark fixed app>
            <v-toolbar-side-icon @click.stop="drawer = !drawer" color="black"></v-toolbar-side-icon>
            <v-toolbar-title style="color:black"> PayMoni</v-toolbar-title>
        </v-toolbar>
    </v-layout>
</template>
<script>
import firebase from 'firebase'
import router from '../router'
export default {
    name: 'Nav',
  data: () =>({
    drawer:null,
    email: ''
  }),
  created() {
      firebase.auth().onAuthStateChanged(
          (user) => {
              this.email = user.email
          }
      )
  },
  methods: {
      signOut: function(){
          firebase.auth().signOut().then(
              function(){
                  router.replace('/')
              }
          )
      }
  }
}
</script>

<style scoped>

    .links {
        text-decoration: none;
        color: black;
    }
</style>



