<template>
    <v-layout>
        <gmap-map ref="maptin" :center="center" :zoom="14" class="google-map">
            <gmap-info-window :options="infoOptions" :position="infoWindowPos" :opened="infoWinOpen" @closeclick="infoWinOpen=false">
                {{infoContent}} <br>
                <v-btn @click="routeCalc">Get Direction</v-btn>
            </gmap-info-window>
            <gmap-marker :key="index" v-for="(m, index) in markers" :position="m.position" :clickable="true" @dblclick="pop(m)" @click="toggleInfoWindow(m, index)">
                
            </gmap-marker>
            <gmap-custom-marker :marker='center'> 
                <img :src="images.userloc" width="50px" height="50px"/>
            </gmap-custom-marker>
        </gmap-map>
    </v-layout>
</template>

<script>
import firebase from 'firebase'
import { db } from "../main";
import GmapCustomMarker from 'vue2-gmap-custom-marker';
export default {
    name: "GoogleMap",
    components:{
        'gmap-custom-marker': GmapCustomMarker
    },
    data() {
        return {
            images:{
			userloc: require('../assets/img/userLocation.png'),
		},
            center: { lat:7.516539, lng:4.528529},
            witpoint:'',
            available:null,
            availAmount:null,
            infoContent: '',
            infoWindowPos: null,
            infoWinOpen:false,
            witPointId:'',
            acctCode:'',
            destination:null,
            phoneNo:'',
            currentMidx:null,
            infoOptions: {
                pixelOffset: {
                    width: 0,
                    height: -35
                }
            },
            markers: [
                /**{
                position:{
                lat:7.517762,
                lng:4.514670
            },
            infoText: 'ETF Hall Buttery',
            infoId: '',
            available:'',
            
            },*/
            {
                position: {
                    lat:7.519980,
                    lng:4.515265
                },
                infoText: 'Awo Blk6 Rm101',
                infoId: 'fIw2EPvbuYhqQ2kszGbij7DbYeN2',
                available:'',
                acctCode:'RS_883134A005ADF4EE238E681BDE6463A5',
                phoneNo:'+2349056346992'
            },
            /**{
                position: {
                    lat:7.518045,
                    lng:4.517654
                },
                infoText: 'Syntax Printing @ Faj',
                infoId: '',
                available:'',
            },*/
            {
                position: {
                    lat:7.520096,
                    lng:4.518379
                },
                infoText: 'A&G @ Thrillhouse',
                infoId: 'vx7ZF9z5w5Z0PjWJHNSsmh6afzl2',
                available:'',
                acctCode:'RS_3BEEE01A016A9AC9CCF8D11EA7BD6833',
                phoneNo:'+2348176587185'
            },
            {
                position: {
                    lat:7.522037,
                    lng:4.516550
                },
                infoText: 'Shegaks @ PG Hall',
                infoId: 'DQEGCejHl9WhsU4s4PxsmglKUer2',
                available:'',
                acctCode:'RS_09168BD6EB39689132802C0A94E4BAB4',
                phoneNo:''
            },
            {
                position: {
                    lat:7.517675,
                    lng:4.512694
                },
                infoText: 'Vast @ New Market',
                infoId: 'cYSDGQoqoyXZteU7F1R7xlFPjqZ2',
                available:'',
                acctCode:'RS_2A8F81D5FF78CEF7FA027BC5F05FF0C9',
                phoneNo:'+2348052307565',
            },
            {
                position: {
                    lat:7.500561,
                    lng:4.521907
                },
                infoText: 'Stella Hostel Buttery @ Maintenance',
                infoId: 'vUZgHR5XxxN1iPKCznDvc8cmnzB2',
                hey: 'hgkjvv',
                available:'',
                acctCode:'RS_E3083146E2E28FE0E93EDB017CCCF4CC',
                phoneNo:'+2347015423552'
            },
            /** {
                position: {
                    lat:7.495154,
                    lng:4.520461
                },
                infoText: 'Asherifa',
                infoId:''
            },*/
            ],
            places: [],
            
           // currentPlace: null
        };
    },

    created () {
        this.geolocate();
    },

    methods: {
        /**setPlace(place){
            this.currentPlace = place;
        },
        addMarker () {
            if(this.currentPlace) {
                const marker = {
                    lat: this.currentPlace.geometry.location.lat(),
                    lng: this.currentPlace.geometry.location.lng()
                };
                this.markers.push({ position: marker },);
                this.places.push(this.currentPlace);
                this.center = marker;
                this.currentPlace = null
            }
        },**/

        geolocate: function () {
            navigator.geolocation.getCurrentPosition(position => {
                this.center = {
                    lat: position.coords.latitude,
                    lng: position.coords.longitude
                };
               

                
            });
        
        },
        toggleInfoWindow: function(marker, idx) {
            this.infoWindowPos = marker.position;
            this.infoContent = marker.infoText;
            this.witPointId = marker.infoId;
            this.acctCode = marker.acctCode;
            this.phoneNo = marker.phoneNo
            db.collection('witpoints').doc(marker.infoId).get().then(
                (doc) => {
                    this.available=doc.data()
                    
                }
            )
 //check if its the same marker that was selected if yes toggle
            if(this.currentMidx == idx) {
                this.infoWinOpen = !this.infoWinOpen;
            }    
            //if different marker set infowindow to open and reset current marker index
            else{
                this.infoWinOpen = true;
                this.currentMidx = idx;
            }
        },
        
        pop:function(marker) {
            this.destination = marker.position;
            db.collection('witpoints').doc(marker.infoId).get().then(
                (doc) => {
                    this.available=doc.data().available
                    this.availAmount = doc.data().availAmount
                    this.witpoint = this.infoContent
            let witPoint = this.witpoint
            let witPointId = this.witPointId
            let available = this.available
            let availAmount = this.availAmount
            let acctCode = this.acctCode
            let witPhoneNo = this.phoneNo
            this.$emit('pickPoint', {witPoint:witPoint, witPointId: witPointId, available:available, availAmount:availAmount, acctCode:acctCode, witPhoneNo:witPhoneNo})
            
                }
            )
            
        },
        routeCalc: function(){
            var directionsService = new google.maps.DirectionsService;
            var directionsDisplay = new google.maps.DirectionsRenderer;

            directionsDisplay.setMap(this.$refs.maptin.$mapObject);

            function calcDisRoute(directionsService, directionsDisplay, start, destination) {
                directionsService.route({
                    origin:start,
                    destination:destination,
                    travelMode:'WALKING'
                }, function(response, status) {
                    if(status == 'OK') {
                        directionsDisplay.setDirections(response);
                    }else {
                        alert('Directions request failed due to '+status);
                    }
                });
            }
            calcDisRoute(directionsService, directionsDisplay, this.center, this.destination /** this.markers[0].position, this.markers[4].position */)
        }
    } 
}
</script>

<style scoped>
    .google-map{
        width: 100%;
        height: 600px;
        margin: 0 auto;
        background: gray;
    }
</style>



