import Vue from 'vue'
import Router from 'vue-router'
import HomeView from '@/components/HomeView'
import StartView from '@/components/StartView'
//import LoginView from '@/components/LoginView'
import TransactionView from '@/components/TransactionView'
import ProfileView from '@/components/ProfileView'
import EditProfileView from '@/components/EditProfileView'
import TransactionPageView from '@/components/TransactionPageView'
import PointHomeView from '@/components/PointHomeView'
import PointProfileView from '@/components/PointProfileView'
import EditPointProfileView from '@/components/EditPointProfileView'
import PointTransactionView from '@/components/PointTransactionView'
//import PointLoginView from '@/components/PointLoginView'
import AdminHomeView from '@/components/AdminHomeView'
import AdminProfileView from '@/components/AdminProfileView'
import EditAdminProfileView from '@/components/EditAdminProfileView'
import AdminTransactionView from '@/components/AdminTransactionView'
import WitPointsView from '@/components/WitPointsView'
import UsersView from '@/components/UsersView'
import AdminLogin from '@/components/AdminLogin'
import firebase from 'firebase'
import { db } from '../main'
import NProgress from 'vue-nprogress'



Vue.use(Router)

let router =  new Router({
  routes: [
    {
      path: '*',
      redirect: '/' 
    },
    {
      path: '/admin_login',
      name: 'adminLogin',
      component: AdminLogin
    }, 
    {
      path: '/',
      name: 'start',
      component: StartView
    }, 
   /** {
      path: '/getin',
      name: 'login',
      component: LoginView
    }, */
    {
      path: '/home',
      name: 'home',
      component: HomeView,
      beforeEnter: (to, from, next) => {
        NProgress.start();
        firebase.auth().onAuthStateChanged(
          (user) => {
            db.collection('users').doc(user.uid).get().then(
              (doc)=> {
                if(doc.exists){
                  next()
                }else {
                  alert('Not Authorized')
                  next('/')
                }
              }
            )
          }
        )
      }
    },
    {
      path: '/profile',
      name: 'profile',
      component: ProfileView,
      beforeEnter: (to, from, next) => {
        firebase.auth().onAuthStateChanged(
          (user) => {
            db.collection('users').doc(user.uid).get().then(
              (doc)=> {
                if(doc.exists){
                  next()
                }else {
                  alert('Not Authorized')
                  next('/')
                }
              }
            )
          }
        )
      }
    },
    {
      path: '/edit_profile',
      name: 'editProfile',
      component: EditProfileView,
      beforeEnter: (to, from, next) => {
        firebase.auth().onAuthStateChanged(
          (user) => {
            db.collection('users').doc(user.uid).get().then(
              (doc)=> {
                if(doc.exists){
                  next()
                }else {
                  alert('Not Authorized')
                  next('/')
                }
              }
            )
          }
        )
      }
    },
    {
      path: '/transactions',
      name: 'transactions',
      component: TransactionView,
      beforeEnter: (to, from, next) => {
        firebase.auth().onAuthStateChanged(
          (user) => {
            db.collection('users').doc(user.uid).get().then(
              (doc)=> {
                if(doc.exists){
                  next()
                }else {
                  alert('Not Authorized')
                  next('/')
                }
              }
            )
          }
        )
      }
    },
    {
      path: '/transaction/:transId',
      name:'transView',
      component: TransactionPageView,
      beforeEnter: (to, from, next) => {
        firebase.auth().onAuthStateChanged(
          (user) => {
            db.collection('users').doc(user.uid).get().then(
              (doc)=> {
                if(doc.exists){
                  next()
                }else {
                  alert('Not Authorized')
                  next('/')
                }
              }
            )
          }
        )
      }
    },
    {
      path: '/point_home',
      name: 'pointHome',
      component: PointHomeView,
      beforeEnter: (to, from, next) => {
        firebase.auth().onAuthStateChanged(
          (user) => {
            db.collection('witpoints').doc(user.uid).get().then(
              (doc)=> {
                if(doc.exists){
                  console.log(user.uid)
                  next()
                 
                }else {
                  alert('Not Authorized')
                  next('/')
                }
              }
            )
          }
        )
      }
    },
    {
      path: '/point_profile',
      name: 'pointProfile',
      component: PointProfileView,
      beforeEnter: (to, from, next) => {
        firebase.auth().onAuthStateChanged(
          (user) => {
            db.collection('witpoints').doc(user.uid).get().then(
              (doc)=> {
                if(doc.exists){
                  next()
                }else {
                  alert('Not Authorized')
                  next('/')
                }
              }
            )
          }
        )
      }
    },
    {
      path: '/edit_point_profile',
      name: 'EditPointProfile',
      component: EditPointProfileView,
      beforeEnter: (to, from, next) => {
        firebase.auth().onAuthStateChanged(
          (user) => {
            db.collection('witpoints').doc(user.uid).get().then(
              (doc)=> {
                if(doc.exists){
                  next()
                }else {
                  alert('Not Authorized')
                  next('/')
                }
              }
            )
          }
        )
      }
    },
    {
      path: '/point_transactions',
      name: 'pointTransactions',
      component: PointTransactionView,
      beforeEnter: (to, from, next) => {
        firebase.auth().onAuthStateChanged(
          (user) => {
            db.collection('witpoints').doc(user.uid).get().then(
              (doc)=> {
                if(doc.exists){
                  next()
                }else {
                  alert('Not Authorized')
                  next('/')
                }
              }
            )
          }
        )
      }
    },
    /**{
      path: '/point_login',
      name: 'pointLogin',
      component: PointLoginView,
    }, */
    {
      path: '/admin_home',
      name: 'admin',
      component: AdminHomeView,
      meta:{
        requiresAuth: true
      }
    },
    {
      path: '/admin_profile',
      name: 'adminProfile',
      component: AdminProfileView,
      meta:{
        requiresAuth: true
      }
    },
    {
      path: '/edit_admin_profile',
      name: 'EditAdminProfile',
      component: EditAdminProfileView,
      meta:{
        requiresAuth: true
      }
    },
    {
      path: '/admin_transactions',
      name: 'adminTransactions',
      component: AdminTransactionView,
      meta:{
        requiresAuth: true
      }
    },
    {
      path: '/witpoints',
      name: 'witPoints',
      component: WitPointsView,
      meta:{
        requiresAuth: true
      }
    },
    {
      path: '/users',
      name: 'users',
      component: UsersView,
      meta:{
        requiresAuth: true
      }
    },
    
  ]
})

/**router.beforeEach((to,from,next) => {
  let currentUser;
  let typeofUser;
  firebase.auth().onAuthStateChanged(
    (user) => {
      currentUser = user
      db.collection('users').doc(currentUser.uid).get().then(
        (doc) => {
          if(doc.exists){
            typeofUser ='User'
            alert(typeofUser)
          }else {
            db.collection('witpoints').doc(currentUser.uid).get().then(
              (doc) => {
                if(doc.exists){
                  typeofUser = 'Witpoint'
                  alert(typeofUser)
                }
              }
            )
          }
        }
      )
    }
  )
  let requiresUserAuth = to.matched.some(record => record.meta.requiresUserAuth);
  let requiresPointAuth = to.matched.some(record => record.meta.requiresPointAuth);

  if((requiresUserAuth || requiresPointAuth) && !currentUser ) next('getin')
  else if ((requiresUserAuth && currentUser) && typeofUser != 'User') next('getin')
  else if ((requiresPointAuth && currentUser) && typeofUser !='Witpoint') next('getin')
  else if ((requiresUserAuth && currentUser) && typeofUser == 'User') next()
  else if ((requiresPointAuth && currentUser) && typeofUser == 'Witpoint') next()
  else next()
}) */


export default router


