<template>
    <div>
        <p-nav-vue></p-nav-vue>  
        <v-container grid-list-xs>
            <v-layout row wrap>
                <v-flex xs12 sm6 md6> 
                    <v-card>
                        <v-container>
                            <v-card-title class="card-name">Confirm a Withdrawal</v-card-title>
                            <v-text-field prepend-icon="compare_arrows" v-model="transID" label="Transaction ID"/>
                            <v-text-field prepend-icon="credit_card" v-model="witID" label="Withdraff6500wal ID"/>
                            <v-btn class="white--text" color="#3D38CB" v-on:click="confirmTrans">Confirm</v-btn>
                        </v-container>
                    </v-card>
                </v-flex>
                 <v-flex xs12 sm6 md6> 
                    <v-card>
                        <v-container>
                            <v-card-title class="card-name">Available Cash</v-card-title>
                            <v-card-text class="texts" style="font-size:30px"> <span></span>{{availAmount}} </v-card-text>
                            <v-text-field prepend-icon="data_usage" label="Enter amount" v-model.number="updtdAmnt"/>
                            <v-btn class="white--text" color="#3D38CB" v-on:click="updatedAmount">Update</v-btn>
                        </v-container>
                    </v-card>
                </v-flex>
                <v-flex xs12 sm6 md6> 
                    <v-card>
                        <v-container>
                            <v-card-title class="card-name">Transactions Overview</v-card-title>
                            <v-container grid-list-xs>
                                <v-layout row wrap>
                                    <v-flex xs12 sm6 md4>
                                        <v-progress-circular :rotate="360" :size="100" :width="12" :value="100" color="blue">
                                            {{confirmed}}
                                        </v-progress-circular><br><br>
                                        <p class="texts"> Confirmed Transactions</p>
                                    </v-flex>
                                     <v-flex xs12 sm6 md4>
                                        <v-progress-circular :rotate="360" :size="100" :width="12" :value="100" color="#ff6500">
                                            {{unconfirmed}}
                                        </v-progress-circular><br><br>
                                        <p class="texts">Unconfirmed Transactions</p>
                                    </v-flex>
                                     <v-flex x12 sm6 md4>
                                        <v-progress-circular :rotate="360" :size="100" :width="12" :value="100" color="red">
                                            {{pending}}
                                        </v-progress-circular><br><br>
                                        <p class="texts">Pending Transactions</p>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                        </v-container>
                    </v-card>
                </v-flex>
            </v-layout>
        </v-container> 
    </div>

</template>

<script>
import PNav from './PointNav'
import {db} from '../main'
import firebase from 'firebase'
import router from '../router'
export default {
    components: {
        'p-nav-vue': PNav
    },
    data: function(){
        return {
            amount:0,
            witpoint:'',
            userTransStat: false,
            pointTransStat: false,
            userID:'',
            transID:'',
            witID:'',
            status:'',
            pending:0,
            unconfirmed:0,
            confirmed:0,
            availAmount:0,
            updtdAmnt:0,
            
        }
    },
    created(){
        this.pendingTrans();
        this.unconfirmedTrans();
        this.confirmedTrans();
        firebase.auth().onAuthStateChanged(
            (user) => {
                let currentUser = user.uid;
                db.collection('witpoints').doc(currentUser).get().then(
                    (doc) => {
                        this.availAmount = doc.data().availAmount
                    }
                )
            }
        )
    },
    methods: {
        pendingTrans:function(){
            var pending =[]
           firebase.auth().onAuthStateChanged(
               (user) => {
                   db.collection('transactions').where('witPointId', '==', user.uid).where('pointTransStat', '==', false).where('userTransStat', '==', false).get().then(
                       (querySnapshot) => {
                           querySnapshot.forEach(
                               (doc) =>{
                                   pending.push(doc.data())
                                   this.pending = pending.length
                                   
                               }
                               
                           )
                       }
                   )
                   
               }
            
           )
        },
        unconfirmedTrans:function(){
            var unconfirmed =[]
           firebase.auth().onAuthStateChanged(
               (user) => {
                   db.collection('transactions').where('witPointId', '==', user.uid).where('pointTransStat', '==', false).where('userTransStat', '==', true).get().then(
                       (querySnapshot) => {
                           querySnapshot.forEach(
                               (doc) =>{
                                   unconfirmed.push(doc.data())
                                   this.unconfirmed = unconfirmed.length
                                   
                               }
                               
                           )
                       }
                   )
                   
               }
            
           )
        },
        confirmedTrans:function(){
            var confirmed =[]
           firebase.auth().onAuthStateChanged(
               (user) => {
                   db.collection('transactions').where('witPointId', '==', user.uid).where('pointTransStat', '==', true).where('userTransStat', '==', true).get().then(
                       (querySnapshot) => {
                           querySnapshot.forEach(
                               (doc) =>{
                                   confirmed.push(doc.data())
                                   this.confirmed = confirmed.length
                                
                               }
                               
                           )
                       }
                   )
                   
               }
            
           )
        },
        confirmTrans: function() {
            firebase.auth().onAuthStateChanged(
                (user) => {
                    let currentUser = user.uid
            db.collection('transactions').doc(this.transID).get().then(
                (doc) => {
                    let datab = doc.data();
                    if(doc.exists) {
                        if(datab.witPointId == currentUser){
                            if(datab.witID == this.witID){
                                db.collection('transactions').doc(this.transID).set({
                                    pointTransStat: true
                                },{merge: true} )
                               
                                this.$toasted.global.transConfirmed().goAway(5000)
                                console.log('confirmed')
                            }else{
                                this.$toasted.global.notRight().goAway(5000)
                            }
                        }else{
                            this.$toasted.global.notAllowedTrans().goAway(5000)
                            console.log('You are not allowed to confirm this transaction')
                        }
                    }else {
                        console.log('Hmm. Na')
                        this.$toasted.global.notRight().goAway(5000)
                    }
                }
            )
            }
            )
        },
        updatedAmount: function() {
            firebase.auth().onAuthStateChanged(
                (user) => {
                    let currentUser = user.uid;
                    this.availAmount = this.updtdAmnt
            db.collection('witpoints').doc(currentUser).set({
                availAmount:this.availAmount
            },{merge: true})
             this.$toasted.global.updatedAmount().goAway(3000)
                }
            )
        }
    }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css?family=Noto+Serif+JP');
@import url('https://fonts.googleapis.com/css?family=Bitter');

.card-name{
        font-size: 25px;
        font-family: 'Bitter', serif;
    }
.texts {
    font-family: 'Noto Serif JP', serif;
}
</style>



