<template>
    <div>
        <nav-vue></nav-vue>
        <v-container>
            <v-layout justify-center>
                <v-container>
                    <v-card>
                        <v-card-text>
                            <h2 class="titles">Transaction Page</h2>
                            <p class="texts" v-if="userTransStat == false">Go to the withdrawal point to get your cash</p>
                            <p class="texts"><b>Transaction ID: </b>{{transID}}</p>
                            <p class="texts"><b>Amount: </b>{{amount}}</p>
                            <p class="texts"><b> Withdrawal Point: </b> {{ witpoint }} </p>
                            <p class="texts"><b> Status: </b> {{ status }} </p>
                            <v-layout justify-center align-center>
                                <p class="texts"> <b>Withdrawal ID:</b> <span v-show="witIdShow"> {{witID}} </span></p>
                                <v-tooltip v-show="btnShow" top color="black" dark >
                                    <v-icon color="warning" dark slot="activator">info</v-icon>
                                    <span >* Only generate the withdrawal id after you have received your cash; as a confirmation.</span>
                                </v-tooltip>
                                <v-btn v-show="btnShow" v-on:click="generateWitId" color="#9c27b0" class="white--text"> Generate </v-btn>
                            </v-layout>
                        </v-card-text>
                    </v-card>
                </v-container>
            </v-layout>
        </v-container>
    </div>
</template>

<script>
import Nav from "./Nav";
import { EventBus } from "./EventBus";
import { db } from "../main";
import firebase from 'firebase'
import router from '../router'
export default {
  components: {
    "nav-vue": Nav
  },
  data: function () {
        return {
            amount:0,
            witpoint:'',
            userTransStat: false,
            pointTransStat: false,
            userID:'',
            transID:'',
            witID:'',
            witIdShow: false,
            btnShow:false,
            status:'',
        }
        
    },
  created() {
      db.collection('transactions').doc(this.$route.params.id).get().then(
          (doc) => {
              if(doc.exists){
                  if(doc.data().userTransStat == false){
                      this.status = 'Pending'
                      this.witIdShow = false;
                      this.btnShow = true
                  }else if (doc.data().userTransStat == true){
                      this.witIdShow = true
                      this.btnShow = false
                      this.status = 'Confirmed'
                  }
                  this.amount = doc.data().amount;
                  this.witpoint = doc.data().witPoint;
                  this.userTransStat = doc.data().userTransStat;
                  this.pointTransStat = doc.data().pointTransStat;
                  this.userID = doc.data().userID;
                  this.transID = doc.id;
                  this.witID = doc.data().witID
                  
              }else {
                  console.log('An Error Occured')
              }
          }
      )
      
  },
  methods: {
        eventTrigger: function () {
            this.status = 'Hooray'
        },
       generateWitId: function() {
          this.witIdShow = true
          this.status='Confirmed'
          this.userTransStat = true
          this.btnShow=false
          db.collection('transactions').doc(this.transID).set({
              userTransStat: this.userTransStat
              
          }, {merge: true})
          console.log(this.userTransStat)
          console.log(this.witID)
      },
        
    }
      
};
</script>

<style scoped>
 @import url('https://fonts.googleapis.com/css?family=Noto+Serif+JP');
  @import url('https://fonts.googleapis.com/css?family=Bitter');
.titles {
  font-family: "Bitter", serif;
}

.texts {
  font-family: "Noto Serif JP", serif;
}
</style>





