<template>
        <v-layout class="bkgrnd">
            <v-layout class="overlay">
                <v-container align-center justify-center>
                    <p class="title-text">Get In!</p>
                    <p class="i-subtitle-text">Dish the cash, get the money</p>
                   <!-- <p class="subtitle-text">Withdraw your cash with ease</p>-->
                    <v-layout align-center justify-center>
                        <v-flex xs12 sm10 md8>
                            <v-card class="elevation-15">
                                <v-card-text>
                                    <v-form>
                                        <v-text-field v-model="email" prepend-icon="account_balance" name="e-mail" label="Email" type="email"></v-text-field>
                                        <v-text-field v-model="password" id="password" prepend-icon="lock" name="password" label="Password" type="password"></v-text-field>
                                    </v-form>
                                </v-card-text>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                   
                                        <v-btn v-on:click="pGetIn" id="gt-strt-btn">Get In </v-btn>
                                   
                                    <v-spacer></v-spacer>
                                </v-card-actions>
                            </v-card>
                        </v-flex>
                    </v-layout>
            </v-container>
            </v-layout>
            

        </v-layout>
</template>

<script>
import firebase from 'firebase'
import router from '../router'
import {db} from '../main'
export default {
    name:'pointLogin',
    data: function() {
        return{
            email:'',
            password:''
        }
    },
    components: {
        
    },
    methods: {
        pGetIn: function() {
            firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(
                (user) => {
                    alert('You dey there')
                    router.replace('admin_home')
                    let currentUser = firebase.auth().currentUser
                },
                (err) => {
                   alert('Idiot, You know be admin')
                }
            )
        }
    }
}
</script>

<style scoped>
 @import url('https://fonts.googleapis.com/css?family=Bitter');
@import url('https://fonts.googleapis.com/css?family=Playfair+Display');
 @import url('https://fonts.googleapis.com/css?family=Dancing+Script');
 
*{
    color: white;
}
.bkgrnd{
    background-color: gray;
    background: url('../assets/adminback.jpg');
     background-repeat:no-repeat; 
     background-size: cover; 
     background-attachment: fixed; 
     background-position-x: center; 
     background-position-y: center;
    height: 100vh;
}
.overlay{
    height: 100vh;
    width: 100%;
    position: absolute;
    background: #ff66007c;
}

.title-text{
    font-family: 'Bitter', serif;
    font-size: 70px;
}

.i-subtitle-text{
    font-family: 'Dancing Script', cursive;
    font-size: 20px;
}

#gt-strt-btn{
    font-family: 'Playfair Display';
    background-color: #9c27b0;
    color:white
}

.subtitle-text{
    font-family: 'Playfair Display', serif;
    font-size: 30px;
}
</style>





