
import {Line} from 'vue-chartjs'
export default {
    extends: Line,
    data () {
        return {
             datacollection: {
              labels: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sept", "Oct", "Nov", "Dec"],
              datasets: [{
                  label: 'Users',
                  borderColor: 'black',
                  data: [15, 20, 25, 29, 40, 100, 250, 300, 350, 450, 551, 649],
          },
          {
            label: 'Points',
            borderColor:'red',
            data: [5, 20, 25, 40, 90, 97, 150, 250, 300, 320, 359, 420]
          },
          

        ]
      }
        }
    },
    mounted () {
        this.renderChart(this.datacollection, {responsive: true, maintainAspectRatio: false})
    }
}




