<template>
    <v-layout>
        <v-navigation-drawer fixed v-model="drawer" app>
            <v-container fluid>
            <v-layout justify-center>
                <v-flex text-xs-center>
                <v-avatar slot="activator" size="100px">
                    <img src="https://image.freepik.com/vetores-gratis/modelo-organico-logotipo-restaurante-de-comida_23-2147492558.jpg" alt="Avatar Image">
                </v-avatar><br><br>
                <v-text>{{point_name}}</v-text>
                </v-flex>
            </v-layout>
            </v-container>
            <v-list dense>
            <router-link class="links" to="/point_home">
                <v-list-tile>
                    <v-list-tile-action>
                    <v-icon>account_balance</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                    <v-list-tile-title>Home</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </router-link>
            <router-link class="links" to="/point_profile">
                <v-list-tile>
                    <v-list-tile-action>
                    <v-icon>shopping_basket</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                    <v-list-tile-title>Profile</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </router-link>
            <router-link class="links" to="/point_transactions">
                <v-list-tile>
                    <v-list-tile-action>
                    <v-icon>transfer_within_a_station</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                    <v-list-tile-title>Transactions</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </router-link>
            <a class="links" href="mailto:paymoning@gmail.com">
                <v-list-tile>
                    <v-list-tile-action>
                    <v-icon>contact_mail</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                    <v-list-tile-title>Contact PayMoni</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </a>
            
                <v-list-tile v-on:click='signedOut'>
                    <v-list-tile-action>
                    <v-icon>power_settings_new</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                    <v-list-tile-title>Logout</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
           
            </v-list>
            <v-container fluid>
            <v-layout justify-start>
                <v-footer color="#3D38CB" app>
                <span class="white--text">&copy; 2018 PayMoni Ltd.</span>
                </v-footer>
            </v-layout>
            </v-container>
        </v-navigation-drawer>
        <v-toolbar color="#3D38CB" dark fixed app >
            <v-toolbar-side-icon @click.stop="drawer = !drawer" color="black"></v-toolbar-side-icon>
            <v-toolbar-title> PayMoni</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items > 
                <p >{{available}}</p>
                <v-container align-content-center>
                 <v-switch class="white--text"  v-model="available"></v-switch>
                 
                </v-container>
            </v-toolbar-items>
            
        </v-toolbar>
    </v-layout>
</template>
<script>
import { db } from "../main";
import firebase from 'firebase'
import router from '../router'
export default {
    name: 'Nav',
  data: () =>({
    drawer:null,
    point_name:'Complete you profile',
    available:false,
  }),
  
  created() {
      firebase.auth().onAuthStateChanged(
          (user) => {
              db.collection('witpoints').doc(user.uid).get().then(
                  (doc) => {
                      this.point_name = doc.data().point_name
                      this.available= doc.data().available
                  }
              )
          }
      )
  },
  watch:{
      available(newValue){
          if(newValue == true){
              
              firebase.auth().onAuthStateChanged(
                  (user) => {
                      db.collection('witpoints').doc(user.uid).set({
                          available: this.available
                      }, {merge: true})
                  }
              )
              
          }else if(newValue == false){
              
              firebase.auth().onAuthStateChanged(
                  (user) => {
                      db.collection('witpoints').doc(user.uid).set({
                          available: this.available
                      }, {merge: true})
                  }
              )
              alert('You are now offline')
          }
      }
  },
  methods: {
    signedOut: function(){
          firebase.auth().signOut().then(
              function(){
                  router.replace('/')
              }
          )
      }
  }
    
}
</script>

<style scoped>

    .links {
        text-decoration: none;
        color: black;
    }
</style>



