<template>
<div>
    <nav-vue></nav-vue>
    <v-container>
        <v-toolbar>
            <v-spacer></v-spacer>
            <v-toolbar-title class="edt-prof-txt"> Transactions </v-toolbar-title>
            <v-spacer></v-spacer>
        </v-toolbar>
        <v-data-table hide-actions :headers="headers" :items="transactions" class="elevation-1">
            <template slot="items" slot-scope="props">
                <td><v-btn class="white--text" color="#A841BA" v-on:click="displayTrans(props.item.transID)" small>View </v-btn></td>
                <td> {{ props.item.transID }} </td>
                <td class="text-xs-right">{{ props.item.amount }}</td>
                <td class="text-xs-right">{{ props.item.witPoint }}</td>
                <td class="text-xs-right">{{ props.item.transDate }}</td>
                <td class="text-xs-right">{{ props.item.status }}</td>
                
            </template>
        </v-data-table>
    </v-container>
    </div>
</template>

<script>
import Nav from "./Nav";
import Vue from 'vue';
import { db } from "../main";
import firebase from 'firebase';
import router from '../router'
export default {
    components: {
        'nav-vue': Nav
    },
    data(){  
        return {
            headers:[
                {

                },
            {
                text:'Transaction  ID',
                align: 'left',
                sortable: false,
                value: 'transID'
            },
            {text:'Amount', value:'amount'},
            {text: 'Withdrawal Point', value:'witPoint'},
            {text: 'Transaction Date', value:'transDate'},
            {text: 'Status', value:'status'}
            ],
            transactions: []
        }
    },
    created(){
        firebase.auth().onAuthStateChanged(
            (user) => {
                db.collection('transactions').where("userID", "==", user.uid).get().then(
            (querySnapshot) => {
                querySnapshot.forEach(
                    (doc) => {
                        
                        var transUserStat='';
                        if(doc.data().userTransStat == true){
                            transUserStat =''
                        } else if(doc.data().userTransStat ==false){
                            transUserStat =''
                        }
                        this.transactions.push(
                            {
                    value: false,
                    transID: doc.id,
                    amount: doc.data().amount,
                    witID: doc.data().witID,
                    witPoint: doc.data().witPoint,
                    transDate: doc.data().transDate,
                    status: transUserStat
                }
                        )

                    })
            }).catch( (error) => {
                console.log(error)
            })
            }
        )
        
    },
    methods: {
        displayTrans: function(id) {
            router.push({name:'transView', params: {id:id}})
        }
    }
}
</script>

<style scoped>
    @import url('https://fonts.googleapis.com/css?family=Bitter');

    .edt-prof-txt {
        font-family: 'Bitter', serif;
    }
</style>





