
import {Bar} from 'vue-chartjs'
export default {
    extends: Bar,
    data () {
        return {
             datacollection: {
              labels: ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"],
              datasets: [{
                  label: 'Taichi Groceries',
                  backgroundColor: 'black',
                  data: [4, 19, 3, 5, 2, 3, 8],
          },
          {
            label: 'The Chop Eatery',
            backgroundColor: 'red',
            data: [5, 23, 7, 5, 26, 12, 29],
          },
          {
            label: 'Filling Station du Ife',
            backgroundColor: 'blue',
            data: [10, 11, 30, 35, 26, 19, 12],
          },

        ]
      }
        }
    },
    mounted () {
        this.renderChart(this.datacollection, {responsive: true, maintainAspectRatio: false})
    }
}




