<template>
<div>
    <p-nav-vue></p-nav-vue>
    <v-container grid-list-xl text-xs-center>
        <v-toolbar>
            <v-spacer></v-spacer>
            <v-toolbar-title class="edt-prof-txt"> Edit Business Profile </v-toolbar-title>
            <v-spacer></v-spacer>
        </v-toolbar>
                        <v-layout row wrap>
                            <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title">Business Name: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                    <v-text-field solo-inverted="" class="texts black--text" :value="point_name" v-model="point_name"></v-text-field>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title">Email: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                    <v-text-field solo-inverted="" class="texts black--text" :value="point_email" v-model="point_email"></v-text-field>
                            </v-flex>
                             <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title">Phone No: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                <v-text-field solo-inverted="" class="texts black--text" :value="point_phone_no" v-model="point_phone_no"></v-text-field>
                            </v-flex>
                            
                             <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title">Acct No: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                    <v-text-field solo-inverted="" class="texts black--text" :value="point_acct_no" v-model="point_acct_no"></v-text-field>
                            </v-flex>
                        </v-layout>
                        <v-btn large color="#ff6500" class="white--text" v-on:click="updatePointProfile"> Update </v-btn>
                    </v-container>
</div>
</template>

<script>
import PNav from './PointNav'
import { db } from "../main";
import firebase from 'firebase'
import router from '../router'
export default {
    components: {
        'p-nav-vue': PNav
    },
    data: function (){
        return {
            point_name:'',
            point_email:'',
            point_phone_no:0,
            point_acct_no:'',
            witPointId:''
        }
    },
    created() {
        firebase.auth().onAuthStateChanged(
            (user) => {
                this.point_email = user.email;
                this.witPointId = user.uid;
                db.collection('witpoints').doc(this.witPointId).get().then(
            (doc) => {
                if(doc.exists){
                    this.point_name = doc.data().point_name;
                    this.point_phone_no = doc.data().point_phone_no;
                    this.point_acct_no = doc.data().point_acct_no
                } else {
                    console.log('Dude, I can\'t find the sh*t')
                }
            }
        )
            }
        )
        
    },
    methods: {
        updatePointProfile: function () {
            firebase.auth().currentUser.updateEmail(this.point_email).then(
                () => {
                    db.collection('witpoints').doc(this.witPointId).set({
                point_name: this.point_name,
                point_phone_no: this.point_phone_no,
                point_acct_no: this.point_acct_no,
                point_email: this.point_email
            }, {merge: true})
            this.$toasted.global.profileUpdate().goAway(3000)
            router.replace('point_profile')
                }
            )
             
           
        }
    }
}
</script>

<style scoped>
    @import url('https://fonts.googleapis.com/css?family=Noto+Serif+JP');
    @import url('https://fonts.googleapis.com/css?family=Bitter');
.texts{
    font-family: 'Noto Serif JP', serif;
}

    .edt-prof-txt {
        font-family: 'Bitter', serif;
    }
</style>





