
import {Line} from 'vue-chartjs'
export default {
    extends: Line,
    data () {
        return {
             datacollection: {
              labels: ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"],
              datasets: [{
                  label: 'Transactions',
                  borderColor: 'orange',
                  data: [20, 40, 150, 200, 50, 90, 250],
          },
          
          

        ]
      }
        }
    },
    mounted () {
        this.renderChart(this.datacollection, {responsive: true, maintainAspectRatio: false})
    }
}




