<template>
    <div>
        <ad-nav></ad-nav>

        <v-container grid-list-xs>
            <v-layout row wrap>
                <v-flex xs12 sm6 md6> 
                    <v-card>
                        <v-container>
                            <v-card-title class="card-name">Confirm a Withdrawal</v-card-title>
                            <v-text-field prepend-icon="compare_arrows" label="Transaction ID"/>
                            <v-text-field prepend-icon="credit_card" label="Withdrawal ID"/>
                            <v-btn class="white--text" color="#ff6500">Confirm</v-btn>
                        </v-container>
                    </v-card>
                </v-flex>
                 <v-flex xs12 sm6 md6> 
                    <v-card color="black" class="white--text">
                        <v-container>
                            <v-card-title class="card-name">Transactions Overview</v-card-title>
                            <v-container grid-list-xs>
                                <v-layout row wrap>
                                    <v-flex xs12 sm6 md4>  
                                        <v-progress-circular :rotate="360" :size="100" :width="12" :value="100" color="blue">
                                            200
                                        </v-progress-circular><br><br>
                                        <p class="texts"> Confirmed Transactions</p>
                                    </v-flex>
                                     <v-flex xs12 sm6 md4>
                                        <v-progress-circular :rotate="360" :size="100" :width="12" :value="100" color="#ff6500">
                                            30
                                        </v-progress-circular><br><br>
                                        <p class="texts">Unconfirmed Transactions</p>
                                    </v-flex>
                                     <v-flex x12 sm6 md4>
                                        <v-progress-circular :rotate="360" :size="100" :width="12" :value="100" color="red">
                                            15
                                        </v-progress-circular><br><br>
                                        <p class="texts">Pending Transactions</p>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                        </v-container>
                    </v-card>
                </v-flex>
                <v-flex xs12 sm6 md6> 
                    <v-card class="">
                        <v-container>
                            <v-card-title class="card-name">Pending Transactions</v-card-title>
                            <v-data-table hide-actions :headers="headers" :items="transactions" class="elevation-1">
                                <template slot="items" slot-scope="props">
                                    <td> {{ props.item.transID }} </td>
                                    <td class="text-xs-right">{{ props.item.amount }}</td>
                                    <td class="text-xs-right">{{ props.item.witID }}</td>
                                    <td class="text-xs-right">{{ props.item.witPoint }}</td>
                                    <td class="text-xs-right">{{ props.item.transDate }}</td>
                                    
                                </template>
                            </v-data-table>
                        </v-container>
                    </v-card>
                </v-flex>
                <v-flex xs12 sm6 md6> 
                    <v-card>
                        <v-container>
                            <v-card-title class="card-name">Unconfirmed Transactions</v-card-title>
                             <v-data-table hide-actions :headers="headers" :items="transactions" class="elevation-1">
                                <template slot="items" slot-scope="props">
                                    <td> {{ props.item.transID }} </td>
                                    <td class="text-xs-right">{{ props.item.amount }}</td>
                                    <td class="text-xs-right">{{ props.item.witID }}</td>
                                    <td class="text-xs-right">{{ props.item.witPoint }}</td>
                                    <td class="text-xs-right">{{ props.item.transDate }}</td>
                                </template>
                            </v-data-table>
                        </v-container>
                    </v-card>
                </v-flex>
               
                 <v-flex xs12 sm12 md12> 
                    <v-card class="">
                        <v-container>
                            <v-card-title class="card-name">Withdrawal Points Stats</v-card-title>
                            <vue-chart></vue-chart>
                            Weekly Transactions Report
                         </v-container>
                    </v-card>
                </v-flex>
                <v-flex xs12 sm6 md6> 
                    <v-card class="">
                        <v-container>
                            <v-card-title class="card-name">PayMoni Growth Metric</v-card-title>
                            <g-vue-graph></g-vue-graph>
                            
                         </v-container>
                    </v-card>
                </v-flex>
                <v-flex xs12 sm6 md6> 
                    <v-card class="">
                        <v-container>
                            <v-card-title class="card-name">PayMoni Transactions Metric</v-card-title>
                            <t-vue-graph></t-vue-graph>
                            
                         </v-container>
                    </v-card>
                </v-flex>
            </v-layout>
        </v-container> 
    </div>
</template>

<script>
import AdNav from './AdminNav'
import VueChart from './Chart'
import GVueGraph from './GrowthGraph'
import TVueGraph from './TransGraph'
export default {
    components: {
        'ad-nav': AdNav,
        'vue-chart' : VueChart,
        'g-vue-graph': GVueGraph,
        't-vue-graph': TVueGraph
    },
     data(){
        return {
            headers:[
            {
                text:'Transaction  ID',
                align: 'left',
                sortable: false,
                value: 'transID'
            },
            {text:'Amount', value:'amount'},
            {text: 'Withdrawal ID', value:'witID'},
            {text: 'Withdrawal Point', value:'witPoint'},
            {text: 'Transaction Date', value:'transDate'},
            ],
            transactions: [
                
                {
                    value: false,
                    transID: 'AXD12345DE',
                    amount: 5000,
                    witID: '!@x1',
                    witPoint:'Awo Lower Buttery',
                    transDate: '2018-01-02',
                    status:''
                },
                {
                    value: false,
                    transID: 'ABW5563VF',
                    amount: 1500,
                    witID: 'x$0y',
                    witPoint:'Awo Self Help Buttery',
                    transDate: '2018-02-12',
                    status:''
                },
                {
                    value: false,
                    transID: 'BBW5563YX',
                    amount: 1000,
                    witID: 'qq#@',
                    witPoint:'Thrillhouse',
                    transDate: '2018-06-04',
                    status:''
                },
            ]
        }
    }
}
</script>

<style scoped>
    @import url('https://fonts.googleapis.com/css?family=Noto+Serif+JP');
    @import url('https://fonts.googleapis.com/css?family=Bitter');

    .card-name{
        font-size: 25px;
        font-family: 'Bitter', serif;
    }
    .texts {
        font-family: 'Noto Serif JP', serif;
    }

</style>



