<template>
<div>
    <nav-vue></nav-vue>
    <v-container grid-list-xl text-xs-center>
        <v-toolbar>
            <v-spacer></v-spacer>
            <v-toolbar-title class="edt-prof-txt"> Edit Profile </v-toolbar-title>
            <v-spacer></v-spacer>
        </v-toolbar>
                        <v-layout row wrap>
                            <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title">Name: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                <v-text-field solo-inverted="" class="texts black--text" :value="name" v-model="name"></v-text-field>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title">Email: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                <v-text-field solo-inverted="" class="texts black--text" :value="email" v-model="email"></v-text-field>
                            </v-flex>
                             <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title">Phone No: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                 <v-text-field solo-inverted="" class="texts black--text" :value="phone_no" v-model="phone_no"></v-text-field>
                            </v-flex>
                            
                             <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title">Birthday: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                 <v-text-field solo-inverted="" class="texts black--text" :value="birthday" v-model="birthday"></v-text-field>
                            </v-flex>
                        </v-layout>
                        <v-btn large color="#9c27b0" class="white--text" v-on:click="updateProfile"> Update </v-btn>
                    </v-container>
</div>
</template>

<script>
import Nav from './Nav'
import { db } from "../main";
import firebase from 'firebase'
import router from '../router'
export default {
    components: {
        'nav-vue': Nav
    },
    data : function() {
        return {
            name:'',
            email:'',
            userID:'',
            phone_no:0,
            birthday:''
        }
    },
    created() {
        firebase.auth().onAuthStateChanged(
            (user) => {
                this.userID = user.uid;
                this.email = user.email
                db.collection('users').doc(this.userID).get().then(
            (doc)=> {
                if(doc.exists){
                    this.name= doc.data().name;
                    this.phone_no = doc.data().phone_no;
                    this.birthday = doc.data().birthday;
                }
            }
        )
            }
        )
        
        console.log(this.email)
    },
    computed: {
        valu: function () {
            return this.email
        }
    },
    methods: {
        updateProfile: function () {
            firebase.auth().currentUser.updateEmail(this.email).then(
                () => {
                    db.collection('users').doc(this.userID).set({
                name: this.name,
                phone_no: this.phone_no,
                birthday: this.birthday,
                email: this.email
            }, {merge: true})
            this.$toasted.global.profileUpdate().goAway(3000)
            router.replace('profile')
                }
            )
             
           
        }
    }
}
</script>

<style scoped>
    @import url('https://fonts.googleapis.com/css?family=Noto+Serif+JP');
    @import url('https://fonts.googleapis.com/css?family=Bitter');
.texts{
    font-family: 'Noto Serif JP', serif;
}

    .edt-prof-txt {
        font-family: 'Bitter', serif;
    }
</style>





