<template>
    <div>
        <nav-vue></nav-vue>
        <v-layout>
            <v-container>
                <v-toolbar>
            <v-spacer></v-spacer>
            <v-toolbar-title class="card-name">Profile </v-toolbar-title>
            <v-spacer></v-spacer>
        </v-toolbar>
                <v-card id="card-info">
                    <v-container>
                    <v-avatar size="200px">
                    <img id="" src="https://www.soccercric.com/uploads/news/images/1309164275952e2e897191.png"/>
                    <v-btn fab small color="#00FFFF" bottom right absolute @click="alert('Change Picture')">
                        <v-icon color="white">camera_alt</v-icon>
                    </v-btn>
                    </v-avatar>
                        <v-card-text class="card-name">{{name}}</v-card-text>
                    </v-container>
                    
                </v-card><br>    
                <v-layout>
                    <v-spacer></v-spacer>
                    <router-link class="links" to="/edit_profile">
                        <v-btn color="#00FFFF" @click="alert('Change Picture')">
                            <v-icon color="white">edit</v-icon>
                        </v-btn>
                    </router-link>
                </v-layout>
                <v-layout>
                    <v-container grid-list-xl text-xs-center>
                        <v-layout row wrap>
                            <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title" >Email: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="texts black--text">{{email}}</v-card-text>
                                </v-card>
                            </v-flex>
                             <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title">Phone No: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="texts black--text">{{phone_no}}</v-card-text>
                                </v-card>
                            </v-flex>
                            
                             <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title">Birthday: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="texts black--text">{{birthday}}</v-card-text>
                                </v-card>
                            </v-flex>
                        </v-layout>
                    </v-container>
                </v-layout>
            </v-container>
        </v-layout>
    </div>
</template>

<script>
import Nav from './Nav'
import firebase from 'firebase'
import { db } from "../main";
export default {
    components:{
        'nav-vue':Nav
    },
    data: function() {
        return {
            name:'',
            email:'',
            phone_no:0,
            birthday:'',
            userID:''
        }
    },
    created() {
        firebase.auth().onAuthStateChanged(
            (user) => {
                this.email = user.email;
                this.userID = user.uid;
                db.collection('users').doc(this.userID).get().then(
            (doc) => {
                if(doc.exists){
                    this.name = doc.data().name;
                    this.phone_no = doc.data().phone_no;
                    this.birthday = doc.data().birthday
                } else {
                    console.log('Dude, I can\'t find the sh*t')
                }
            }
        )
            }
        )
        
    }
}
</script>

<style scoped>
    @import url('https://fonts.googleapis.com/css?family=Noto+Serif+JP');
    @import url('https://fonts.googleapis.com/css?family=Bitter');
   
    

    #card-info{
        background:rgb(233, 233, 233);
        color: black;
    }
    .card-name{
        font-size: 25px;
        font-family: 'Bitter', serif;
    }
    .texts{
        font-family: 'Noto Serif JP', serif;
    }

    .links {
        text-decoration: none;
        color: black;
    }
</style>





