<template>
    <div>
        <ad-nav-vue></ad-nav-vue>
        <v-container grid-list-xs>
            <v-layout row wrap>
                 <v-flex xs12 sm12 md12 >
                    <v-card >
                        <v-container >
                        <v-card-title  class="card-name">All Users</v-card-title>
                        </v-container>
                    </v-card>
                </v-flex>
                <v-flex xs12 sm6 md6 v-for="(user, index) in users " :key= index> 
                    <v-card >
                        <v-container>
                            <v-card-title class="card-name">{{user.name}}</v-card-title>
                                <v-avatar size="200px">
                                    <img id="" :src="user.pics"/>
                                </v-avatar>
                                <br>
                            <v-badge overlap color="red"> 
                                <span slot="badge">{{user.allTrans}}</span>
                                <v-card-text>All Transactions </v-card-text>
                            </v-badge>
                            <v-badge overlap color="red"> 
                                <span slot="badge">{{user.monthTrans}}</span>
                                <v-card-text>Transactions This Month</v-card-text>
                            </v-badge>
                            <v-badge overlap color="red"> 
                                <span slot="badge">{{user.weekTrans}}</span>
                                <v-card-text>Transactions This Week</v-card-text>
                            </v-badge>
                            <v-badge overlap color="red"> 
                                <span slot="badge">{{user.todayTrans}}</span>
                                <v-card-text>Transactions Today</v-card-text>
                            </v-badge>
                            <v-btn color="#9c27b0" dark > View Transactions </v-btn>
                            <v-btn color="#ff6500" dark> View Profile </v-btn>
                        </v-container>
                    </v-card>
                </v-flex>
            </v-layout>
        </v-container>  
    </div>
</template>

<script>
import AdNav from './AdminNav'
import user from '../user'
export default {
    components:{
        'ad-nav-vue': AdNav
    },
   data() {
       return {
           'users':user.users
       }
   }
}
</script>

<style scoped>
    @import url('https://fonts.googleapis.com/css?family=Noto+Serif+JP');
    @import url('https://fonts.googleapis.com/css?family=Bitter');

    .card-name{
        font-size: 25px;
        font-family: 'Bitter', serif;
    }
    .texts {
        font-family: 'Noto Serif JP', serif;
    }

</style>



