<template>
    <div>
        <p-nav-vue></p-nav-vue>
        <v-layout>
            <v-container>
                <v-toolbar>
            <v-spacer></v-spacer>
            <v-toolbar-title class="card-name">Business Profile </v-toolbar-title>
            <v-spacer></v-spacer>
        </v-toolbar>
                <v-card id="card-info">
                    <v-container>
                    <v-avatar size="200px">
                    <img id="" src="https://image.freepik.com/vetores-gratis/modelo-organico-logotipo-restaurante-de-comida_23-2147492558.jpg"/>
                    <v-btn fab small color="#3D38CB" bottom right absolute @click="alert('Change Picture')">
                        <v-icon color="white">camera_alt</v-icon>
                    </v-btn>
                    </v-avatar>
                        <v-card-text class="card-name">{{point_name}}</v-card-text>
                    </v-container>
                    
                </v-card><br>
                <v-layout>
                    <v-spacer></v-spacer>
                    <router-link class="links" to="/edit_point_profile">
                        <v-btn color="#3D38CB" @click="alert('Change Picture')">
                            <v-icon color="white">edit</v-icon>
                        </v-btn>
                    </router-link>
                </v-layout>
                <v-layout>
                    <v-container grid-list-xl text-xs-center>
                        <v-layout row wrap>
                            <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title" >Email: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="texts black--text">{{point_email}}</v-card-text>
                                </v-card>
                            </v-flex>
                             <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title">Phone No: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="texts black--text">{{point_phone_no}}</v-card-text>
                                </v-card>
                            </v-flex>
                             <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="black--text title">Acct No: </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs6 sm6 md6>
                                 <v-card dark color="rgb(233, 233, 233)">
                                    <v-card-text class="texts black--text">{{point_acct_no}}</v-card-text>
                                </v-card>
                            </v-flex>
                        </v-layout>
                    </v-container>
                </v-layout>
            </v-container>
        </v-layout>
    </div>
</template>

<script>
import PNav from './PointNav'
import { db } from "../main";
import firebase from 'firebase'
export default {
    components:{
        'p-nav-vue':PNav
    },
    data: function() {
        return {
            point_name:'',
            point_email:'',
            point_phone_no:0,
            point_acct_no:'',
            witPointId:''
        }
    },
    created() {
        firebase.auth().onAuthStateChanged(
            (user) => {
                this.point_email = user.email;
                this.witPointId = user.uid;
                db.collection('witpoints').doc(this.witPointId).get().then(
            (doc) => {
                if(doc.exists){
                    this.point_name = doc.data().point_name;
                    this.point_phone_no = doc.data().point_phone_no;
                    this.point_acct_no = doc.data().point_acct_no
                } else {
                    console.log('Dude, I can\'t find the sh*t')
                }
            }
        )
            }
        )
        
    }
}
</script>

<style scoped>
    @import url('https://fonts.googleapis.com/css?family=Noto+Serif+JP');
    @import url('https://fonts.googleapis.com/css?family=Bitter');
   
    

    #card-info{
        background:rgb(233, 233, 233);
        color: black;
    }
    .card-name{
        font-size: 25px;
        font-family: 'Bitter', serif;
    }
    .texts{
        font-family: 'Noto Serif JP', serif;
    }

    .links {
        text-decoration: none;
        color: black;
    }
</style>





